import React from 'react'
import TestimonialCard from './TestimonialCard'
import testimonials from '../data/testimonials.json'

type Testimonial = {
  id: string
  name: string
  role?: string
  quote: string
  avatar?: string
  title?: string
}

export default function Testimonials(): React.ReactElement {
  const items: Testimonial[] = (testimonials as unknown as Testimonial[]) || []

  return (
    <section className="py-16 px-4 bg-gray-50">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl font-bold mb-6 text-center text-uswift-navy">What early users say</h2>

        <div className="grid gap-6 grid-cols-1 md:grid-cols-3">
          {items.map((t, i) => (
            <blockquote key={t.id || i} className="bg-white p-6 rounded-lg shadow flex flex-col card-magic">
              <div className="flex items-center gap-4 mb-4">
                <img src={t.avatar} alt={`${t.name} avatar`} className="w-12 h-12 rounded-full object-cover" />
                <div>
                  <div className="font-semibold text-gray-900">{t.name}</div>
                  <div className="text-sm text-gray-600">{t.title || t.role}</div>
                </div>
              </div>
              <p className="text-gray-800">“{t.quote}”</p>
            </blockquote>
          ))}
        </div>
      </div>
    </section>
  )
}
